.search-container {
    width: 100%;
    height: 600px;
    background: url(../../assets/HealthInspectorBGSmall.jpg) no-repeat center bottom;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-transition: height .5s;
    -o-transition: height .5s;
    transition: height .5s;
}

@media(max-width: 450px) {
  .search-container {
    height: 100vh;
  }
}

.search-logo-container-hide {
  display: none;
  height: 0px;
  width: 0px;
}

.search-container-small {
    height: 150px;
}


